@import "https://fonts.googleapis.com/css2?family=Open+Sans&display=swap";
@import "https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&display=swap";
* {
  color: #d8d8d8;
  -webkit-tap-highlight-color: transparent;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

body {
  height: auto;
  width: 100vw;
  background-color: #28282a;
  font-family: Open Sans, sans-serif;
}

.content {
  z-index: 1;
  margin: 0 1.5em;
}

@media screen and (min-width: 750px) {
  .content {
    margin: 0 25vw;
  }
}

.button-container {
  height: auto;
  width: auto;
  justify-content: center;
  align-items: center;
  display: flex;
}

.btn {
  height: 3em;
  width: 12em;
  letter-spacing: .2em;
  text-transform: uppercase;
  border-radius: 2em;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  transition: all .5s;
  display: flex;
}

@media screen and (min-width: 750px) {
  .btn {
    font-size: 1.1em;
  }
}

::-webkit-scrollbar {
  width: 10px;
  background-color: #000;
}

::-webkit-scrollbar-thumb {
  background: #bfb7a4;
  border-radius: 10px;
}

input[type="range"] {
  appearance: none;
  width: 100%;
  height: 1.4em;
  cursor: pointer;
  opacity: .7;
  background: #171817;
  border: none;
  border-radius: 1.4em;
  transition: all .2s;
  box-shadow: inset 2px 2px 3px #0000003f;
}

input[type="range"]::-webkit-slider-thumb {
  appearance: none;
  height: 1.9em;
  width: 1.9em;
  background-color: #fff;
  border-radius: 50%;
}

input[type="range"]:hover {
  opacity: 1;
  transition: all .2s;
}

input[type="color"] {
  width: 100%;
  background: none;
  border: none;
}

input[type="color"]::-webkit-color-swatch {
  height: 2em;
  width: 100%;
  border: none;
  border-radius: 2em;
}

select {
  text-align: center;
  height: 2em;
  opacity: .6;
  background: #131414;
  border: none;
  border-radius: 4em;
}

option {
  height: 10em;
  margin: 2em 0;
}

select:hover {
  opacity: 1;
}

.about {
  color: #bfb7a4;
  border: 2px solid #bfb7a4;
}

@media screen and (min-width: 750px) {
  .about {
    opacity: .5;
  }

  .about:hover {
    opacity: 1;
    color: #28282a;
    background-color: #bfb7a4;
  }
}

line {
  stroke: #171817;
}

@media screen and (min-width: 750px) {
  line {
    stroke: beige;
  }
}

@media screen and (min-width: 750px) {
  svg {
    opacity: .3;
    transition: all .6s;
  }

  svg:hover {
    opacity: 1;
    transition: all .6s;
  }
}

body {
  overflow-x: hidden;
}

.viewer {
  max-width: 100vw;
  margin-top: 1.5em;
  display: block;
  position: relative;
}

.canvas-container {
  z-index: 1;
  width: 100vw;
  height: 50vh;
  background-color: #28282a;
  position: fixed;
  top: -1.5em;
  overflow: hidden;
  box-shadow: 0 8px 24px #0000004d;
}

@media screen and (min-width: 750px) {
  .canvas-container {
    width: 50vw;
    height: 55vh;
    margin: 0 25vw;
  }
}

.canvas-container .canvas-viewer {
  z-index: 10000;
  height: 100%;
  width: 100%;
}

#infoContainer {
  z-index: 100;
  width: 100vw;
  height: 100vh;
  text-align: center;
  background-color: #1d1d1df2;
  transition: all .5s;
  display: block;
  position: fixed;
  top: 0;
  left: 100vw;
}

@media screen and (min-width: 750px) {
  #infoContainer {
    left: 80vw;
    width: 20vw !important;
  }

  #infoContainer h1 {
    color: #fff;
  }
}

#infoContainer .info-content-container {
  width: 100%;
  height: 100%;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
}

#infoContainer .info-content-container .hedding {
  padding: 4em 2em 2em;
  display: block;
}

#infoContainer .info-content-container div {
  grid-template-columns: 1fr 2fr;
  padding: 2em 3em;
  display: grid;
}

#infoContainer .info-content-container .label {
  opacity: .4;
  padding-left: 2em;
}

#infoContainer .info-content-container .data {
  text-align: right;
  padding-right: 2em;
}

#infoContainer .toggle-menue {
  height: 20vh;
  width: 20px;
  z-index: 1000;
  opacity: .5;
  background-color: #958768ce;
  border-radius: 2em 0 0 2em;
  position: fixed;
  top: 50%;
  right: 0;
  overflow-x: hidden;
  transform: translateY(-50%);
}

@media screen and (min-width: 750px) {
  #infoContainer .toggle-menue {
    left: unset;
    display: none;
  }
}

.content {
  margin-top: 53vh;
}

@media screen and (min-width: 750px) {
  .content {
    margin-top: 60vh;
  }
}

.parameter-container {
  width: 100%;
}

.parameter-container .params {
  grid-template-columns: 1fr 2fr;
  margin: 3em 0;
  padding: 0 2em;
  display: grid;
}

@media screen and (min-width: 750px) {
  .parameter-container .params {
    grid-template-columns: 1fr 4fr;
  }
}

.submit {
  color: #28282a;
  opacity: 1;
  background: #bfb7a4;
}

@media screen and (min-width: 750px) {
  .submit {
    opacity: .5;
  }

  .submit:hover {
    opacity: 1;
  }
}

line {
  stroke: #171817;
}

@media screen and (min-width: 750px) {
  line {
    stroke: beige;
  }
}

@media screen and (min-width: 750px) {
  svg {
    opacity: .3;
    transition: all .6s;
  }

  svg:hover {
    opacity: 1;
    transition: all .6s;
  }
}

.move {
  transition: all .5s;
  left: 0 !important;
}

.scroll-lock {
  overflow-y: hidden;
}

